.roadmaps{
    position: relative;
    font-family: "Poppins", sans-serif;
    height: auto;
    background: #fff;
}
.roadmap-img{
  background-image: url('../../assets/roadmap.jpg');
  width: 100%;
  height: 80vh;
  background-color: rgba(0,0,0,0.35);
  z-index: 1;
  background-position: cover;
  background-repeat: no-repeat;
  background-size: cover;
}
.roadmap-title h3{ 
    display: flex;
    align-items: center;
    justify-content: center;
    color: #f1f1f1;
    font-size: 1.5rem;
    font-weight: 900;
    text-transform: uppercase;

}
.roadmap-quote{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 3.5rem;
  font-weight: 900;
  text-transform: uppercase;
  padding-top: 200px;
}

  .swiper-wrapper-roadmap{
    display: flex;
    width: 100%;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
    padding: 50px;
    margin-bottom: 40px;
 
  } 

  .card-roadmap-list .card-roadmap-item {
    height: auto;
    user-select: none;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    backdrop-filter: blur(30px);
    background-image: linear-gradient(to top, #ab00f2, #a101e1, #9702d1, #8d03c1, #8304b1, #7c04a7, #74049d, #6d0493, #66038c, #5f0285, #59017e, #520077);
    border: 1px solid #ffffff80;
    width: 60vh;
  }
  
  .card-roadmap-list .card-roadmap-item .user-image {
      width: 250px;
      height: 200px;
      margin-bottom: 40px;
      border: 2px solid #eff0f7;
  }
  .card-roadmap-list .card-roadmap-item .user-name{
    font-size: 26px;
    color: #ffffff;
    font-weight: 500;
  }
  
  .card-roadmap-list .card-roadmap-item .user-profession {
    font-size: 16px;
    color: #e3e3e3;
    font-weight: 500;
    margin: 14px 0 40px;
    text-wrap: wrap;
  }
  
  .card-roadmap-list .card-roadmap-item .message-button {
    font-size: 16px;
    padding: 10px 15px;
    color: #fff;
    border-radius: 6px;
    font-weight: 500;
    cursor: pointer;
    background: #3a0d82;
    border: 2px solid transparent;
    transition: 0.5s;
  }
  
  .card-roadmap-list .card-roadmap-item .message-button:hover {
    background: #ffffff1a;
    border: 2px solid #fff;
    color: #fff;
  }
  
  .slider-wrapper .swiper-pagination-bullet {
    background: #fff;
    height: 13px;
    width: 13px;
    opacity: 0.5;
  }
  
  .slider-wrapper .swiper-pagination-bullet-active {
    opacity: 1;
  }
  
  .slider-wrapper .swiper-slide-button {
    color: #fff;
    margin-top: -55px;
    transition: 0.5s;
  }
  
  @media (max-width: 768px) {
    .slider-wrapper {
      margin: 0 20px 40px;
    }
    .roadmap-img{
      background-image: url('../../assets/hero.png');
      width: 100%;
      height: 60vh;
      background-size: cover;
    }
    .roadmap-quote{
      font-size: 2.5rem;
      padding-top: 100px;
    }
    .roadmap-title h3{ 
      font-size: 1rem;  
  }

  }